import React from "react"
import {Link} from "gatsby"
import styles from "../styles/toolbar.module.css"

export default (props) => (
    <div className={styles.topRightLink} style={{left: props.left, width: props.width}}>
        <Link className={styles.topRightText} to={props.to} style={{color: props.color}}>
            {props.children}
        </Link>
    </div>
)